/** Media Queries **/
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.testimonal {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 3.5rem;
}
.testimonal blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonal blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonal p {
  text-align: right;
  color: #FFFFFF;
}